<template>
  <div class="container">
    <div class="main-info">
      <ct-section-title class="main-info-title" :title="$t('corpInfo.corpBasicInfo')" />
      <el-row :gutter="20">
        <el-col v-for="obj in infoList" :key="obj.prop" :span="4">
          <CtTooltip :label="obj.label" :content="itemInfoFormatter(obj)"></CtTooltip>
        </el-col>
      </el-row>
      <ParamInfo />
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import ParamInfo from './components/ParamInfo'
export default {
  components: {
    ParamInfo,
  },
  data() {
    return {
      addEditDialogVisible: false,
      modifyType: '',
    }
  },
  computed: {
    ...mapState('user', ['user']),
    infoList() {
      return [
        {
          label: this.$t('corpInfo.corpName'),
          prop: 'offerName',
          formatter: () => this.user.custName,
        },
        {
          label: this.$t('corpInfo.corpId'),
          prop: 'offerNbr',
          formatter: () => this.user.custId,
        },
      ]
    },
  },
  methods: {
    itemInfoFormatter(obj) {
      const des = obj.formatter ? obj.formatter() : '-'
      return des
    },
  },
}
</script>
