<template>
  <div class="table-container">
    <ct-section-title class="section-title" :title="$t('corpInfo.paramInfo')">
      <template slot="rightContent">
        <el-button type="primary" @click="modify(null)">{{
          $t('corpInfo.create')
        }}</el-button>
      </template>
    </ct-section-title>
    <el-table
      ref="table"
      :data="tableData"
      :border="true"
      height="100%"
      :loading="loading"
    >
      <el-table-column
        prop="paramName"
        :label="$t('corpInfo.paramName')"
        show-overflow-tooltip
        resizable
      >
      </el-table-column>
      <el-table-column
        prop="createTime"
        :label="$t('corpInfo.operationTime')"
        show-overflow-tooltip
        resizable
      >
      </el-table-column>
      <el-table-column
        prop="paramValue"
        :label="$t('corpInfo.paramInfo')"
        show-overflow-tooltip
        resizable
      >
      </el-table-column>
      <el-table-column :label="$t('rule.operate')" fixed="right">
        <template slot-scope="scope">
          <div class="action-button-group">
            <el-button type="text" @click="modify(scope.row)">{{
              $t('rule.modify')
            }}</el-button>
            <el-button class="cor-red" type="text" @click="del(scope.row.paramId)">{{
              $t('rule.delete')
            }}</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <AddEditDialog
      :visible.sync="addEditDialogVisible"
      :modify-form="modifyForm"
      @modifyParam="getParamValueByCust()"
    ></AddEditDialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import fetch from '@/request/fetch'
import AddEditDialog from './dialogs/AddEditDialog.vue'
export default {
  components: {
    AddEditDialog,
  },
  props: {
    selectPackageList: {
      type: Array,
      required: true,
      default: () => [],
    },
  },
  data() {
    return {
      loading: false,
      tableData: [],
      addEditDialogVisible: false,
      modifyForm: null,
    }
  },
  computed: {
    ...mapState('user', ['user']),
  },
  mounted() {
    this.getParamValueByCust()
  },
  methods: {
    async getParamValueByCust() {
      const param = {
        paramCode: 'SMS_WEBHOOK_PUSH_URL',
        custId: this.user.custId,
      }
      this.loading = true
      fetch('param/getParamValueByCust', param, { method: 'get' })
        .then((res) => {
          this.tableData = res ? [res] : []
        })
        .finally(() => {
          this.loading = false
        })
    },
    async modify(row) {
      this.addEditDialogVisible = true
      this.modifyForm = row
    },
    async del(paramId) {
      this.$confirm(`${this.$t('corpInfo.deletePushAddress')}？`, this.$t('common.tip'), {
        confirmButtonText: this.$t('rule.delete'),
        cancelButtonText: this.$t('rule.cancellation'),
        type: 'success',
      })
        .then(async () => {
          fetch('param/deleteParamById', { paramId }, { method: 'get' }).then(() => {
            this.$message.success(this.$t('rule.delectSuccess'))
            this.getParamValueByCust()
          })
        })
        .catch(() => {})
    },
  },
}
</script>

<style lang="scss" scoped>
.table-container {
  overflow: auto;
  flex: 1;
  background-color: white;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}
.section-title {
  font-weight: 500;
  font-size: 14px;
  color: #333333;
  margin-bottom: 20px;
}
.action-button-group {
  display: flex;
  align-items: center;
  .el-button {
    padding: 0;
  }
}
</style>
