<template>
  <div>
    <el-dialog
      :title="modifyTitle"
      :visible.sync="v"
      :before-close="handleClose"
      width="520px"
      :close-on-click-modal="false"
      append-to-body
    >
      <el-form
        ref="paramFormRef"
        :model="paramForm"
        :rules="paramRules"
        size="small"
        class="demo-ruleForm"
      >
        <el-form-item :label="$t('corpInfo.pleaseSelectParam')" prop="mobile">
          <ct-select
            v-model="paramForm.paramCode"
            :placeholder="$t('corpInfo.pleaseSelectParam')"
            :options="operaOptions"
            filterable
          >
          </ct-select>
        </el-form-item>
        <el-form-item
          v-if="paramForm.paramCode === 'SMS_WEBHOOK_PUSH_URL'"
          prop="paramValue"
          :label="$t('corpInfo.upSmsPushAddress')"
        >
          <el-input
            v-model="paramForm.paramValue"
            :placeholder="inputPh('corpInfo.upSmsPushAddress')"
            maxlength="1000"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" size="small" @click="clickOk">{{
          $t('common.ok')
        }}</el-button>
        <el-button size="small" @click="cancel">{{ $t('common.cancel') }}</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
export default {
  mixins: [langMixin],
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    modifyForm: {
      type: Object,
      default: () => {
        return null
      },
    },
  },
  data() {
    return {
      paramForm: {
        paramCode: '',
      },
      paramRules: {
        paramValue: [
          {
            required: true,
            message: this.inputPh('corpInfo.upSmsPushAddress'),
            trigger: 'blur',
          },
        ],
      },
      operaOptions: [
        { label: this.$t('corpInfo.upSmsPushAddress'), value: 'SMS_WEBHOOK_PUSH_URL' },
      ],
    }
  },
  computed: {
    ...mapState('user', ['user']),
    modifyTitle() {
      return this.$t('common.tip')
    },
    v: {
      get: function () {
        return this.visible
      },
      set: function (value) {
        this.$emit('update:visible', value)
      },
    },
  },
  watch: {
    visible(val) {
      if (val) {
        if (this.modifyForm) {
          this.paramForm = { ...this.modifyForm }
        } else {
          this.paramForm = {}
        }
      }
    },
  },
  methods: {
    clickOk() {
      this.$refs.paramFormRef.validate(async (valid) => {
        if (!valid) return
        let extraParam = {
          paramCode: 'SMS_WEBHOOK_PUSH_URL',
          corpId: this.user.custId,
          parentUserId: this.user.userId,
        }
        if (!this.modifyForm) {
          extraParam = {
            ...extraParam,
            statusCd: 1000,
            paramType: 1,
            paramName: '上行短信推送地址',
          }
        }
        const url = 'param/saveCmpParam'
        await fetch(url, { ...extraParam, ...this.paramForm }, { method: 'post' })
        this.$refs.paramFormRef.clearValidate()
        const msg = this.modifyForm
          ? this.$t('common.editSuccess')
          : this.$t('common.createSuccess')
        this.$message.success(msg)
        this.v = false
        this.$emit('modifyParam')
      })
    },
    cancel() {
      this.v = false
      this.$refs.paramFormRef.clearValidate()
    },
    handleClose() {
      this.cancel()
    },
    validatePhone(rule, value, callback) {
      if (!this.paramForm.areaCode) {
        callback(new Error(this.$t('accountMag.selectAreaCode')))
      }
      if (!/^\d{1,15}$/.test(value)) {
        callback(new Error(this.$t('accountMag.correctMobile')))
      }
      callback()
    },
  },
}
</script>
<style lang="scss" scoped></style>
